.Home {
  text-align: center;
}

.Home-logo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 1em;
  border-radius: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .Home-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Home-header {
  background-color: #282c34;
  min-height: 65vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Home-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navbar {
  margin: 1em auto;
  /* margin-bottom: 1.5em; */
  color: #61dafb;
  text-align: center;
  font-size: larger;
}

.Nav-link {
  color: #61dafb;
}

.Map {
  margin: 0 5em;
}
